import React from 'react';
// Page Components
import CookiePolicyDialog from '../shared/Dialog/CookiePolicyDialog';  
import CookiePolicyPage from '../pages/common/CookiePolicyPage';

export default function CookiePolicyModal(props) {  
    return (
        <div>
            <CookiePolicyDialog open={props.open} toggle={props.toggle}>
                <CookiePolicyPage />
            </CookiePolicyDialog>
        </div>
    )
}
